button {
  margin: 1em;
  font-size: 1.5em;
}
#jsoxford {
  width: 500px;
  height: 500px;
}
#jsoxford polygon,
#jsoxford path,
#jsoxford g {
  transition: all 1s;
}
.jo-standard-horse {
  transform: translate(-300px, 0);
}
.jo-standard-horse.on {
  transform: translate(0);
}
/* white bars */
.jo-wbar-1,
.jo-wbar-2 {
  transform-origin: 0 800px;
  opacity: 0;
}
.jo-wbar-1.on,
.jo-wbar-2.on {
  transform: none;
  opacity: 0.9;
}
.jo-wbar-1 {
  transform: rotate(-30deg);
}
.jo-wbar-2 {
  transform: rotate(30deg);
}
/* color bars top */
.jo-cbar-1,
.jo-cbar-2 {
  transform-origin: 257px 175px;
  opacity: 0;
}
.jo-cbar-1.on,
.jo-cbar-2.on {
  transform: none;
  opacity: 1;
}
.jo-cbar-1 {
  transform: rotate(-40deg);
}
.jo-cbar-2 {
  transform: rotate(10deg);
}
/* color bars bottom */
.jo-cbar-3,
.jo-cbar-4,
.jo-cbar-5 {
  transform-origin: 257px 175px;
  opacity: 0;
}
.jo-cbar-3.on,
.jo-cbar-4.on,
.jo-cbar-5.on {
  transform: none;
  opacity: 1;
}
.jo-cbar-3 {
  transform: rotate(-40deg);
}
.jo-cbar-4 {
  transform: rotate(10deg);
}
.jo-cbar-5 {
  transform: rotate(-10deg);
}
#woLogo {
  width: 500px;
  height: 289px;
}
#woLogo polygon,
#woLogo path,
#woLogo g {
  transition: all .5s;
  transform-origin: 50%;
}
